<template>
  <div>
    <template>
      <material-card
        color="accent"
        full-header
      >
        <template #heading>
          <v-tabs
            v-model="tab"
            background-color="transparent"
            slider-color="white"
            class="pa-8"
          >
            <v-tab
              href="#menu-9"
              @click="getAll"
            >
              ALL ORDER
            </v-tab>
            <!-- <v-tab
              href="#menu-1"
              @click="getpickingup"
            >
              PICKING UP
            </v-tab>
            <v-tab
              href="#menu-2"
              @click="getpickedup"
            >
              PICKED UP
            </v-tab>
            <v-tab
              href="#menu-3"
              @click="getdeliv_assigned"
            >
              DELIVERY ASSIGNED
            </v-tab> -->
            <v-tab
              href="#menu-4"
              @click="getdelivered"
            >
              COMPLETE
            </v-tab>
            <!-- <v-tab
              href="#menu-5"
              @click="getredelivery"
            >
              RE-DELIVERY
            </v-tab> -->
            <v-tab
              href="#menu-6"
              @click="getretur"
            >
              RETURN
            </v-tab>
            <v-tab
              href="#menu-7"
              @click="getcanceled"
            >
              CANCEL
            </v-tab>
            <v-tab
              href="#menu-8"
              @click="newOrder"
            >
              <v-icon>mdi-plus</v-icon> New Order
            </v-tab>
          </v-tabs>
        </template>

        <v-tabs-items v-model="tab">
          <v-tab-item id="menu-3">
            <delivery-assign-view :data="del_assign" />
          </v-tab-item>
          <v-tab-item id="menu-4">
            <delivered-view :data="delivered" />
          </v-tab-item>
          <v-tab-item id="menu-6">
            <return-view :data="retur" />
          </v-tab-item>
          <v-tab-item id="menu-7">
            <canceled-view :data="canceled" />
          </v-tab-item>
          <v-tab-item id="menu-9">
            <all-order-view :data="all" />
          </v-tab-item>
          <v-tab-item
            id="menu-8"
            active-class="v-window-item--active"
          >
            <new-order-view
              :data="users"
              :district="district"
              :village="village"
              :methods="methods"
              :del_fee_list="del_fee_list"
              :category_id="category_id"
              :city="city"
            />
          </v-tab-item>
        </v-tabs-items>
      </material-card>
    </template>
  </div>
</template>

<script>
  import axios from 'axios'
  // import HtmlPdf from '../Print/pdf.vue'
  import DeliveryAssignView from './DeliveryAssigned.vue'
  import DeliveredView from './Delivered.vue'
  import ReturnView from './Return.vue'
  import CanceledView from './Canceled.vue'
  import NewOrderView from './NewOrderView.vue'
  import AllOrderView from './AllOrder.vue'

  export default {
    name: 'RegulerView',
    components: {
      DeliveryAssignView,
      DeliveredView,
      ReturnView,
      CanceledView,
      NewOrderView,
      AllOrderView,

    },
    data () {
      return {
        selected: [],
        tab: null,
        search: '',
        sortable: false,
        rules: [
          value =>
            !value ||
            value.size < 2000000 ||
            'Avatar size should be less than 2 MB!',
        ],
        category_id: 1,
        pickingup: [],
        pickedup: [],
        del_assign: [],
        delivered: [],
        all: [],
        retur: [],
        canceled: [],
        users: [],
        city: [],
        district: [],
        village: [],
        methods: [],
        del_fee_list: [],
        reDelivery: [],
        editedIndex: -1,
        editedItem: {
          id: '',
          no_order: '',
          client: '',
          delivery_fee: '',
          order_status: '',
          payment_status: '',
          payment_method: '',
        },
      }
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    mounted () {
      this.newOrder()
      this.getAll()
    },

    methods: {

      // getpickingup () {
      //   this.selected = []
      //   axios.get('admin/order/pickup').then(res => {
      //     this.pickingup = res.data.data
      //   })
      // },

      // getpickedup () {
      //   this.selected = []
      //   axios.get('admin/order/picked-up').then(res => {
      //     this.pickedup = res.data.data
      //   })
      // },
      getdeliv_assigned () {
        this.selected = []
        axios.get('admin/order/delivery-assigned').then(res => {
          this.del_assign = res.data.data
        })
      },
      getdelivered () {
        this.selected = []
        axios.get('admin/order/delivered').then(res => {
          this.delivered = res.data.data
        })
      },

      // getredelivery () {
      //   this.selected = []
      //   axios.get('admin/order/re-delivery').then(res => {
      //     this.reDelivery = res.data.data
      //   })
      // },

      getretur () {
        this.selected = []
        axios.get('admin/order/return').then(res => {
          this.retur = res.data.data
        })
      },
      getAll () {
        this.selected = []
        axios.get('admin/order-exp/all').then(res => {
          this.all = res.data.data
        })
      },
      getcanceled () {
        this.selected = []
        axios.get('admin/order/canceled').then(res => {
          this.canceled = res.data.data
        })
      },
      newOrder () {
        axios.get('admin/user/customer').then(res => {
          this.users = res.data.data
          this.district = res.data.districts
          this.village = res.data.village
          this.methods = res.data.methods
          this.city = res.data.city
          this.del_fee_list = res.data.del_fee_list
          this.$store.commit('district', res.data.districts)
          this.$store.commit('village', res.data.village)
          this.$store.commit('customer_list', res.data.data)
          this.$store.commit('del_fee_list', res.data.del_fee_list)
          this.$store.commit('methods', res.data.methods)
        })
      },
      detailItem (item) {
        this.detailIndex = this.wallet.indexOf(item)
        this.detailItem = Object.assign({}, item)
        this.dialog = true
      },

      editItem (item) {
        this.editedIndex = this.reguler.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.reguler.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.reguler.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.reguler[this.editedIndex], this.editedItem)
        } else {
          this.reguler.push(this.editedItem)
        }
        this.close()
      },
      print () {
        // Pass the element id here
        this.$htmlToPaper('printMe')
      },
    },
  }
</script>
